            'anlogos para efectos legales. En caso de formalizar con Usted la aceptacin de algn producto o servicio ofrecido por Jardepot, ' +
            'sus Datos sern utilizados para el cumplimiento de las obligaciones derivadas de esa relacin jurdica.      ' +
            'Los Datos sern tratados de conformidad con la Ley Federal de Proteccin de Datos Personales en Posesin de los Particulares y su Reglamento.' +
            '      La confidencialidad de los Datos est garantizada y los mismos estn protegidos por medidas de seguridad administrativas, tcnicas y fsicas,' +
            ' para evitar su dao, prdida, alteracin, destruccin, uso, acceso o divulgacin indebida. nicamente las personas autorizadas tendrn acceso a sus Datos.     ' +
            ' En caso de existir alguna modificacin al presente Aviso de Privacidad se har de su conocimiento en nuestro sitio de Internet';
        const dialogRef = this.dialog.open(DialogComponent, {
            panelClass: 'generic-dialog',
            direction: 'ltr'
        });
        dialogRef.componentInstance.title = 'Aviso de privacidad';
        dialogRef.componentInstance.body = textBody;
